@charset "UTF-8";
:root {
  --white-color: #fff;
  --max-width-container: calc(1254px + 30px); }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  color: var(--white-color);
  font-family: "Roboto", sans-serif;
  font-size: 20px; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: inherit; }

.visually-hidden {
  position: absolute !important;
  /*удаляет элемент из потока документа*/
  overflow: hidden;
  /*cкрывает вылезающий за границы контент*/
  clip: rect(1px 1px 1px 1px);
  /*Вырезает ту часть контента, которая должна отображаться. Для IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  /*вырезает ту часть контента, которая должна отображаться*/
  width: 1px;
  /*минимально возможный размер ширины (некоторые скринридеры игнорируют элементы с нулевой высотой и шириной) */
  height: 1px;
  /*минимально возможный размер высоты (некоторые скринридеры игнорируют элементы с нулевой высотой и шириной) */
  padding: 0;
  /*убирает отступы*/
  border: 0;
  /*убирает границы*/ }

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0; }

.site-container {
  overflow: hidden; }

.btns-menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: space-between;
  width: 100%;
  padding: 10px 15px;
  margin-bottom: 45px;
  background-color: rgba(255, 255, 255, 0.4); }

.btn {
  position: relative;
  margin: 5px 0;
  padding: 5px 15px;
  font-size: 18px;
  font-weight: 700;
  border: none;
  cursor: pointer;
  color: var(--white-color);
  text-transform: uppercase; }

.btn::after,
.link::after {
  content: " ";
  position: absolute;
  height: 100%;
  width: 0;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.4);
  transition: all 0.7s ease;
  mix-blend-mode: overlay; }

.btn:hover::after,
.link:hover::after {
  width: 100%; }

.btn:not(:last-child) {
  margin-right: 10px; }

.btn:disabled {
  opacity: 0.4;
  cursor: default; }

.btn:disabled:hover::after {
  width: 0; }

.btn-ruls {
  background-color: #333; }

.link {
  position: relative;
  display: inline-block;
  color: var(--white-color);
  text-transform: uppercase; }

.home {
  margin-right: 10px;
  padding: 5px 15px;
  font-size: 18px;
  font-weight: 700;
  background-color: #333; }

.main {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #782a78;
  background: linear-gradient(285deg, #813281 0%, #f59056 63%, #e9af43 98%); }
  .main__item {
    width: 100%; }
  .main__item:not(:last-child) {
    margin-bottom: 20px; }
  .main__link {
    width: 100%;
    padding: 15px 25px;
    text-align: center;
    font-size: 25px;
    font-weight: 900;
    background-color: rgba(255, 255, 255, 0.4);
    color: #333; }

.tic-tac-toe {
  background: #56b4d3;
  background: linear-gradient(65deg, #56b4d3 7%, #9e6feb 35%, #c15746 90%);
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column; }
  .tic-tac-toe__btn {
    background-color: #1c6d87; }
  .tic-tac-toe__restart {
    background-color: #c15746; }
  .tic-tac-toe__game {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    margin-bottom: 25px; }
  .tic-tac-toe__cell {
    width: 80px;
    height: 80px;
    background-color: rgba(255, 255, 255, 0.4); }
  .tic-tac-toe__win-text {
    margin: 0;
    font-family: "Roboto", serif;
    font-size: 40px;
    text-align: center;
    color: var(--white-color);
    font-weight: 900;
    text-shadow: 3px 3px 7px rgba(0, 0, 0, 0.5); }

.pointer {
  cursor: pointer; }

.circle {
  width: 100%;
  height: 100%;
  stroke-dasharray: 283;
  stroke-dashoffset: 283;
  animation: draw 1s 1.5s forwards; }

.cross {
  width: 100%;
  height: 100%; }

.cross-first {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  animation: draw 0.6s forwards; }

.cross-second {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  animation: draw 0.6s 0.6s forwards; }

@keyframes draw {
  100% {
    stroke-dashoffset: 0; } }

.snake {
  height: 100vh;
  background: #229a87;
  background: linear-gradient(180deg, #cff691 0%, #1f8b7b 63%, #349fdd 100%);
  display: flex;
  align-items: center;
  flex-direction: column; }
  .snake__restart {
    background-color: #9f5e7e; }
  .snake__btn {
    background-color: #ce9360; }
  .snake__score-text {
    margin: 25px 0 0;
    font-family: "Roboto", serif;
    font-size: 30px;
    text-align: center;
    color: var(--white-color);
    font-weight: 900;
    text-shadow: 3px 3px 7px rgba(0, 0, 0, 0.5); }

.popup {
  position: fixed;
  z-index: 50;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.7s ease; }
  .popup.active {
    opacity: 1;
    visibility: visible; }
  .popup__body {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4); }
  .popup__content {
    position: relative;
    padding: 20px 25px 30px;
    overflow-y: auto;
    max-height: 60vh;
    background: #121212;
    background: linear-gradient(58deg, #121212 10%, #333333 98%); }
  .popup__title {
    text-transform: uppercase;
    text-align: center;
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 25px; }
  .popup__text {
    max-width: 75vw; }
    .popup__text p {
      margin: 10px 0; }
  .popup__close-btn {
    position: absolute;
    top: 20px;
    right: 25px;
    padding: 3px 10px;
    cursor: pointer;
    background-color: transparent;
    border: 2px solid var(--white-color);
    color: var(--white-color);
    transition: all 0.7s ease; }
  .popup__close-btn:hover {
    background-color: var(--white-color);
    color: #333; }
